	module.exports = {"title":"Building a Spam Filter","date":"2017-01-08T23:51:47+00:00","layout":"post","path":"/spam-filter/","category":"natural language processing","description":"Tutorial on how to build a spam filter using Python and NLTK.","body":"<p>In this post I will explain how a rudimentary spam filter can be built using Python and a natural language processing library called <a href=\"http://www.nltk.org/\">NLTK</a>.</p>\n<h3>How Does a Spam Filter Work?</h3>\n<p>A spam filter uses a simple technique called <em>classification</em>. Classification just means that something can be placed into a specific group based on its characteristics. In our case we can either classify an email as <strong>spam</strong> or not spam, often called <strong>ham</strong>.</p>\n<p>So how can a computer decipher whether an email is considered spam or ham? A time-tested method that can be used is a <em>Bayesian filter</em>. I will not go into the mathematics behind a Bayesian filter here but if you are interested in learning more about this, an awesome article over at <a href=\"https://betterexplained.com/articles/an-intuitive-and-short-explanation-of-bayes-theorem/\">Better Explained</a> provides a very intuitive explanation.</p>\n<p>We are going to train a classifier that will detect whether an email is spam or ham based on its textual content. Words like <em>viagra</em> and <em>lottery</em> are going to appear more frequently in spam emails than legitimate ones. The classifier will use this knowledge to make its decision when classifying an email.</p>\n<h3>Lets Get Started</h3>\n<h4>Project Structure</h4>\n<pre><code>.\n data\n  enron1\n   ham\n   spam\n  enron2\n   ham\n   spam\n  enron3\n      ham\n      spam\n <span class=\"hljs-class\"><span class=\"hljs-keyword\">lib</span></span>\n  __init__.py\n  model.py\n  shell.py\n main.py\n requirements.txt\n</code></pre>\n<p>The data folder contains three different datasets. The spam and ham folders contain a bunch of <code>.txt</code> files that are the emails. <code>lib/model.py</code> is our machine learning classifier and <code>lib/shell.py</code> is a Python shell that we will use to interact with the trained model. <code>main.py</code> kicks off all the fun.</p>\n<h4>Building the Classifier</h4>\n<h5>Initialization</h5>\n<pre><code class=\"language-Python\">// lib/model.py\n\n<span class=\"hljs-keyword\">from</span> __future__ <span class=\"hljs-keyword\">import</span> print_function, division\n<span class=\"hljs-keyword\">import</span> os\n<span class=\"hljs-keyword\">from</span> collections <span class=\"hljs-keyword\">import</span> Counter\n<span class=\"hljs-keyword\">from</span> string <span class=\"hljs-keyword\">import</span> punctuation\n<span class=\"hljs-keyword\">from</span> random <span class=\"hljs-keyword\">import</span> shuffle, randint\n<span class=\"hljs-keyword\">from</span> nltk <span class=\"hljs-keyword\">import</span> WordNetLemmatizer, word_tokenize\n<span class=\"hljs-keyword\">from</span> nltk.corpus <span class=\"hljs-keyword\">import</span> stopwords\n<span class=\"hljs-keyword\">from</span> nltk <span class=\"hljs-keyword\">import</span> NaiveBayesClassifier, classify\n\n<span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">Model</span><span class=\"hljs-params\">(object)</span>:</span>\n    stoplist = stopwords.words(<span class=\"hljs-string\">'english'</span>)\n    \n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">__init__</span><span class=\"hljs-params\">(self)</span>:</span>\n        self.trainDir = self.getRandomDir()\n        strTrainDir = str(self.trainDir)\n        print(<span class=\"hljs-string\">'Training with enron'</span> + strTrainDir + <span class=\"hljs-string\">' dataset.'</span>)\n        hamPath = <span class=\"hljs-string\">'data/enron'</span> + strTrainDir + <span class=\"hljs-string\">'/ham/'</span>\n        spamPath = <span class=\"hljs-string\">'data/enron'</span> + strTrainDir + <span class=\"hljs-string\">'/spam/'</span>\n        spamFiles = os.listdir(spamPath)\n        hamFiles = os.listdir(hamPath)\n        hamCorpus = self.buildCorpus(hamFiles, hamPath)\n        spamCorpus = self.buildCorpus(spamFiles, spamPath)\n        self.emails = self.labelEmails(spamCorpus, hamCorpus)\n        <span class=\"hljs-keyword\">print</span> (<span class=\"hljs-string\">'CORPUS SIZE: '</span> + str(len(self.emails)) + <span class=\"hljs-string\">' emails\\n'</span>)\n        shuffle(self.emails)\n        \n<span class=\"hljs-meta\">    @staticmethod</span>\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">buildCorpus</span><span class=\"hljs-params\">(fileList, path)</span>:</span>\n        corpus = []\n        <span class=\"hljs-keyword\">for</span> file <span class=\"hljs-keyword\">in</span> fileList:\n            <span class=\"hljs-keyword\">if</span> <span class=\"hljs-keyword\">not</span> file.startswith(<span class=\"hljs-string\">'.'</span>):\n                f = open(path + file, <span class=\"hljs-string\">'r'</span>)\n                corpus.append(f.read())\n        f.close()\n        <span class=\"hljs-keyword\">return</span> corpus\n\n<span class=\"hljs-meta\">    @staticmethod</span>\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">getRandomDir</span><span class=\"hljs-params\">()</span>:</span>\n        numDirs = len([f <span class=\"hljs-keyword\">for</span> f <span class=\"hljs-keyword\">in</span> os.listdir(<span class=\"hljs-string\">'data'</span>) <span class=\"hljs-keyword\">if</span> <span class=\"hljs-keyword\">not</span> f.startswith(<span class=\"hljs-string\">'.'</span>)])\n        <span class=\"hljs-keyword\">return</span> randint(<span class=\"hljs-number\">1</span>, numDirs)\n\n<span class=\"hljs-meta\">    @staticmethod</span>\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">labelEmails</span><span class=\"hljs-params\">(spam, ham)</span>:</span>\n        emails = [(email, <span class=\"hljs-string\">'ham'</span>) <span class=\"hljs-keyword\">for</span> email <span class=\"hljs-keyword\">in</span> ham]\n        emails.extend([(email, <span class=\"hljs-string\">'spam'</span>) <span class=\"hljs-keyword\">for</span> email <span class=\"hljs-keyword\">in</span> spam])\n        <span class=\"hljs-keyword\">return</span> emails\n</code></pre>\n<p>First, we need to initialize everything and correctly label the datasets. The first few lines of code will choose a dataset at random and load it into the program. This dataset will be used to train the classifier. The <code>labelEmails</code> function goes through all the emails and creates two arrays. Each array is an array of tuples with each tuple containing an email and its corresponding label.</p>\n<h5>Preprocessing</h5>\n<pre><code class=\"language-Python\">// lib/model.py\n\n<span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">Model</span><span class=\"hljs-params\">(object)</span>:</span>\n    stoplist = stopwords.words(<span class=\"hljs-string\">'english'</span>)\n    \n    // Previous functions removed\n    // to increase readability\n    \n<span class=\"hljs-meta\">    @staticmethod</span>\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">preprocess</span><span class=\"hljs-params\">(email)</span>:</span>\n        lemmatizer = WordNetLemmatizer()\n        <span class=\"hljs-comment\"># Tokenize email</span>\n        tokens = word_tokenize(unicode(email, errors=<span class=\"hljs-string\">'ignore'</span>), language=<span class=\"hljs-string\">'english'</span>)\n        <span class=\"hljs-comment\"># Remove punctuation from token list</span>\n        tokens = [token <span class=\"hljs-keyword\">for</span> token <span class=\"hljs-keyword\">in</span> tokens <span class=\"hljs-keyword\">if</span> token <span class=\"hljs-keyword\">not</span> <span class=\"hljs-keyword\">in</span> punctuation]\n        <span class=\"hljs-comment\"># Lemmatize the tokens</span>\n        lemmatized = [lemmatizer.lemmatize(token.lower()) <span class=\"hljs-keyword\">for</span> token <span class=\"hljs-keyword\">in</span> tokens]\n        <span class=\"hljs-keyword\">return</span> lemmatized\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">getFeatures</span><span class=\"hljs-params\">(self, email)</span>:</span>\n        <span class=\"hljs-keyword\">return</span> { word: count <span class=\"hljs-keyword\">for</span> word, count <span class=\"hljs-keyword\">in</span> Counter(self.preprocess(email)).items() <span class=\"hljs-keyword\">if</span> <span class=\"hljs-keyword\">not</span> word <span class=\"hljs-keyword\">in</span> self.stoplist }\n</code></pre>\n<p>So now that we have two arrays of spam and ham we need to extract the significant features from each email. We begin by preprocessing the data. In the <code>preprocess</code> function we take the email and split it into a bunch of <em>tokens</em>.</p>\n<pre><code class=\"language-Python\"><span class=\"hljs-meta\">&gt;&gt;&gt; </span>word_tokenize(unicode(<span class=\"hljs-string\">\"Theyve done studies, you know. 60 percent of the time, it works every time.\"</span>, errors=<span class=\"hljs-string\">'ignore'</span>), language=<span class=\"hljs-string\">'english'</span>)\n[<span class=\"hljs-string\">u'Theyve'</span>, <span class=\"hljs-string\">u'done'</span>, <span class=\"hljs-string\">u'studies'</span>, <span class=\"hljs-string\">u','</span>, <span class=\"hljs-string\">u'you'</span>, <span class=\"hljs-string\">u'know'</span>, <span class=\"hljs-string\">u'.'</span>, <span class=\"hljs-string\">u'60'</span>, <span class=\"hljs-string\">u'percent'</span>, <span class=\"hljs-string\">u'of'</span>, <span class=\"hljs-string\">u'the'</span>, <span class=\"hljs-string\">u'time'</span>, <span class=\"hljs-string\">u','</span>, <span class=\"hljs-string\">u'it'</span>, <span class=\"hljs-string\">u'works'</span>, <span class=\"hljs-string\">u'every'</span>, <span class=\"hljs-string\">u'time'</span>, <span class=\"hljs-string\">u'.'</span>]\n</code></pre>\n<p>Next, we strip the tokenized array of all punctuation and lemmatize the tokens. Lemmatizing returns the base for a given word. I.E. footballs becomes football and walking becomes walk. Using the base of each word helps the classifier correctly classify an email.</p>\n<p>The <code>getFeatures</code> function takes the preprocessed email and counts the frequency of each word. Note that if the word is in the <em>stoplist</em> it is not counted. Stopwords are words that have no contextual significance. These are words like a, the, and because.</p>\n<h4>Training the Classifier</h4>\n<pre><code class=\"language-Python\">// lib/model.py\n\n<span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">Model</span><span class=\"hljs-params\">(object)</span>:</span>\n    stoplist = stopwords.words(<span class=\"hljs-string\">'english'</span>)\n    \n    // Previous functions removed\n    // to increase readability\n    \n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">train</span><span class=\"hljs-params\">(self, proportion=<span class=\"hljs-number\">0.8</span>)</span>:</span>\n        print(<span class=\"hljs-string\">'Generating feature sets...'</span>)\n        features = [(self.getFeatures(email), label) <span class=\"hljs-keyword\">for</span> (email, label) <span class=\"hljs-keyword\">in</span> self.emails]\n        trainSize = int(len(features) * proportion)\n        <span class=\"hljs-comment\"># Training set is first 80% of features</span>\n        self.trainSet, self.testSet = features[:trainSize], features[trainSize:]\n        <span class=\"hljs-comment\"># Train the classifier</span>\n        print(<span class=\"hljs-string\">'Training predictive model...'</span>)\n        self.classifier = NaiveBayesClassifier.train(self.trainSet)\n        \n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">evaluate</span><span class=\"hljs-params\">(self)</span>:</span>\n        <span class=\"hljs-comment\"># Check how the classifier performs the test set</span>\n        print(<span class=\"hljs-string\">'\\n----------------- RESULTS -----------------'</span>)\n        <span class=\"hljs-keyword\">print</span> (<span class=\"hljs-string\">'TEST SET ACCURACY:     '</span> + <span class=\"hljs-string\">'{0:.1%}'</span>.format(classify.accuracy(self.classifier, self.testSet)) + <span class=\"hljs-string\">'\\n'</span>)\n\n        self.classifier.show_most_informative_features(<span class=\"hljs-number\">20</span>)\n        print(<span class=\"hljs-string\">'\\n----------------- END RESULTS -------------'</span>)\n</code></pre>\n<p>In the <code>train</code> function we use the <code>getFeatures</code> function we just built. After we have the array of featurized (this is probably not a word) emails and their corresponding labels, we split up the feature set into a training set and a validation set. The general rule of thumb is to use 80% of the set for training and the remaining 20% for validation. The training set is then fed into the classifier. The classifier uses Bayesian filtering on the training set to generate a model which is then returned to us.</p>\n<p><code>evaluate</code> just prints out the accuracy of the model by running it against the validation set we portioned off earlier. We will use this in the next section.</p>\n<h4>Building an Interactive Shell</h4>\n<pre><code class=\"language-Python\">// lib/shell.py\n\n<span class=\"hljs-keyword\">from</span> __future__ <span class=\"hljs-keyword\">import</span> print_function\n<span class=\"hljs-keyword\">from</span> random <span class=\"hljs-keyword\">import</span> choice\n<span class=\"hljs-keyword\">from</span> cmd <span class=\"hljs-keyword\">import</span> Cmd\n<span class=\"hljs-keyword\">import</span> os\n\n<span class=\"hljs-class\"><span class=\"hljs-keyword\">class</span> <span class=\"hljs-title\">Shell</span><span class=\"hljs-params\">(Cmd)</span>:</span>\n\n    prompt = <span class=\"hljs-string\">'&gt; '</span>\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">__init__</span><span class=\"hljs-params\">(self, args)</span>:</span>\n        Cmd.__init__(self)\n        self.model = args\n        self.testDirIdx = self.getRandomTestDir() <span class=\"hljs-comment\">#self.model.trainDir</span>\n        hamPath = <span class=\"hljs-string\">'data/enron'</span> + str(self.testDirIdx) + <span class=\"hljs-string\">'/ham/'</span>\n        spamPath = <span class=\"hljs-string\">'data/enron'</span> + str(self.testDirIdx) + <span class=\"hljs-string\">'/spam/'</span>\n        spamFiles = os.listdir(spamPath)\n        hamFiles = os.listdir(hamPath)\n        hamCorpus = self.model.buildCorpus(hamFiles, hamPath)\n        spamCorpus = self.model.buildCorpus(spamFiles, spamPath)\n        self.testEmails = self.model.labelEmails(spamCorpus, hamCorpus)\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">getRandomTestDir</span><span class=\"hljs-params\">(self)</span>:</span>\n        numDirs = len([f <span class=\"hljs-keyword\">for</span> f <span class=\"hljs-keyword\">in</span> os.listdir(<span class=\"hljs-string\">'data'</span>) <span class=\"hljs-keyword\">if</span> <span class=\"hljs-keyword\">not</span> f.startswith(<span class=\"hljs-string\">'.'</span>)])\n        possTestDirs = range(<span class=\"hljs-number\">1</span>, numDirs + <span class=\"hljs-number\">1</span>)\n        possTestDirs.remove(self.model.trainDir)\n        <span class=\"hljs-keyword\">return</span> choice(possTestDirs)\n\n<span class=\"hljs-meta\">    @staticmethod</span>\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">printEmail</span><span class=\"hljs-params\">(email)</span>:</span>\n        print(<span class=\"hljs-string\">'------- EMAIL -------\\n'</span>)\n        print(email)\n        print(<span class=\"hljs-string\">'\\n------- END EMAIL ---\\n'</span>)\n\n<span class=\"hljs-meta\">    @staticmethod</span>\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">printResults</span><span class=\"hljs-params\">(prediction, actual)</span>:</span>\n        print(<span class=\"hljs-string\">'PREDICT: '</span> + prediction)\n        print(<span class=\"hljs-string\">'ACTUAL:  '</span> + actual + <span class=\"hljs-string\">'\\n'</span>)\n\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">do_random</span><span class=\"hljs-params\">(self, args)</span>:</span>\n        <span class=\"hljs-string\">'''Tests random email against spam filter.'''</span>\n        randomEmail = choice(self.testEmails)\n        self.printEmail(randomEmail[<span class=\"hljs-number\">0</span>])\n        features = self.model.getFeatures(randomEmail[<span class=\"hljs-number\">0</span>])\n        result = self.model.classifier.classify(features)\n        self.printResults(result, randomEmail[<span class=\"hljs-number\">1</span>])\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">do_testall</span><span class=\"hljs-params\">(self, args)</span>:</span>\n        <span class=\"hljs-string\">'''Runs test set against classifier and returns results.'''</span>\n        features = [(self.model.getFeatures(email), label) <span class=\"hljs-keyword\">for</span> (email, label) <span class=\"hljs-keyword\">in</span> self.testEmails]\n        accuracy = self.model.getAccuracy(features)\n        print(<span class=\"hljs-string\">'ACCURACY AGAINST ENRON'</span> + str(self.testDirIdx) + <span class=\"hljs-string\">': '</span> + <span class=\"hljs-string\">'{0:.1%}'</span>.format(accuracy) + <span class=\"hljs-string\">'\\n'</span>)\n\n\n    <span class=\"hljs-function\"><span class=\"hljs-keyword\">def</span> <span class=\"hljs-title\">do_quit</span><span class=\"hljs-params\">(self, args)</span>:</span>\n        <span class=\"hljs-string\">'''Quits the program.'''</span>\n        print(<span class=\"hljs-string\">\"Bye.\"</span>)\n        <span class=\"hljs-keyword\">return</span> <span class=\"hljs-keyword\">True</span>\n</code></pre>\n<p>I wont go into detail about whats going on here but essentially were just building an interactive shell to visually test the model. We grab a random dataset that we didnt use to train the model with and use that to test the classifier. We have the option to feed the model a single email or the whole batch and see the results. I used the Python Cmd library to build the shell. If you want to know more about the library <a href=\"https://pymotw.com/2/cmd/\">PyMOTW</a> has a great tutorial on how to use it.</p>\n<h4>Putting It All Together</h4>\n<pre><code class=\"language-Python\">// main.py\n\n<span class=\"hljs-keyword\">from</span> lib.model <span class=\"hljs-keyword\">import</span> Model\n<span class=\"hljs-keyword\">from</span> lib.shell <span class=\"hljs-keyword\">import</span> Shell\n\n<span class=\"hljs-keyword\">if</span> __name__ == <span class=\"hljs-string\">'__main__'</span>:\n    model = Model()\n    model.train()\n    model.evaluate()\n\n    shell = Shell(model)\n    shell.cmdloop(<span class=\"hljs-string\">'\\nStarting interactive prompt...\\n'</span>)\n</code></pre>\n<p><code>main.py</code> is pretty simple. We just use the methods that we built in the shell and model classes. First, we instantiate the model. Then, we train it and run it against the validation set for evaluation. Lastly, the shell is spawned so we can further play with our classifier.</p>\n<h4>Results</h4>\n<pre><code class=\"language-sh\">(venv) [spam-filter] python main.py                                                                                                                 master  \nTraining with enron3 dataset.\nCORPUS SIZE: 5512 emails\n\nGenerating feature sets...\nTraining predictive model...\n\n----------------- RESULTS -----------------\nTEST SET ACCURACY:     98.5%\n\nMost Informative Features\n              registered = 2                spam : ham    =     99.9 : 1.0\n                 kitchen = 1                 ham : spam   =     99.7 : 1.0\n                    pill = 1                spam : ham    =     98.0 : 1.0\n             promotional = 1                spam : ham    =     92.6 : 1.0\n                     853 = 1                 ham : spam   =     86.5 : 1.0\n                     med = 1                spam : ham    =     83.3 : 1.0\n              medication = 1                spam : ham    =     81.5 : 1.0\n                      em = 1                spam : ham    =     76.0 : 1.0\n                     wil = 1                spam : ham    =     63.2 : 1.0\n                     713 = 1                 ham : spam   =     57.2 : 1.0\n                     mai = 1                spam : ham    =     55.9 : 1.0\n                    1933 = 1                spam : ham    =     50.4 : 1.0\n                  doctor = 1                spam : ham    =     50.0 : 1.0\n                    dave = 1                 ham : spam   =     48.2 : 1.0\n              wrongfully = 1                spam : ham    =     44.9 : 1.0\n                  tablet = 1                spam : ham    =     44.9 : 1.0\n                      wi = 1                spam : ham    =     44.9 : 1.0\n                excelled = 1                spam : ham    =     43.1 : 1.0\n                    jeff = 1                 ham : spam   =     42.9 : 1.0\n                 alcohol = 1                spam : ham    =     41.3 : 1.0\n\n----------------- END RESULTS -------------\n\nStarting interactive prompt...\n\n&gt; \n</code></pre>\n<p>Running <code>python main.py</code> will launch our program and start training the model. We see that the model performed with 98.5% accuracy against the validation set. We also have displayed the top 20 most informative features. Notice words like <em>promotional</em> are 92.6 times as likely to be in a spam email than a ham email. On the other hand, if the email contains a name like <em>Jeff</em> or <em>Dave</em> its probably ham.</p>\n<pre><code>&gt; random\n<span class=\"hljs-comment\">------- EMAIL -------</span>\n\nSubject: sas online tutorial\nhi -\nwe have access <span class=\"hljs-keyword\">to</span> <span class=\"hljs-keyword\">the</span> sas online tutorial <span class=\"hljs-keyword\">for</span> <span class=\"hljs-keyword\">the</span> next <span class=\"hljs-number\">30</span> days . point your\nbrowser <span class=\"hljs-keyword\">to</span>\n<span class=\"hljs-keyword\">and</span> use <span class=\"hljs-keyword\">the</span> username  enron  <span class=\"hljs-keyword\">and</span> password  enron  <span class=\"hljs-keyword\">to</span> enter .\n<span class=\"hljs-keyword\">the</span> way this product works <span class=\"hljs-keyword\">is</span> designed <span class=\"hljs-keyword\">for</span> a single user ( <span class=\"hljs-keyword\">it</span> sets a  cookie \nallowing you <span class=\"hljs-keyword\">to</span>  resume  your place <span class=\"hljs-keyword\">in</span> <span class=\"hljs-keyword\">the</span> tutorial when you re - enter . )\n<span class=\"hljs-keyword\">since</span> several <span class=\"hljs-keyword\">of</span> us may use <span class=\"hljs-keyword\">it</span> , we ' ll need <span class=\"hljs-keyword\">to</span> work <span class=\"hljs-keyword\">around</span> this , each <span class=\"hljs-keyword\">of</span> us\nremembering <span class=\"hljs-keyword\">where</span> we were <span class=\"hljs-keyword\">before</span> <span class=\"hljs-keyword\">and</span> recreating any sample data sets , etc\nnecessary <span class=\"hljs-keyword\">for</span> <span class=\"hljs-keyword\">the</span> lesson <span class=\"hljs-keyword\">in</span> progress .\nclayton\nps <span class=\"hljs-keyword\">the</span> module eis / olap will be a part <span class=\"hljs-keyword\">of</span> our installation next <span class=\"hljs-built_in\">month</span> , <span class=\"hljs-keyword\">but</span> <span class=\"hljs-keyword\">is</span>\n<span class=\"hljs-keyword\">not</span> currently available\npps please remember <span class=\"hljs-keyword\">to</span> use your <span class=\"hljs-keyword\">local</span> browser <span class=\"hljs-keyword\">to</span> browse <span class=\"hljs-keyword\">the</span> sas online\ndocumentation . invoking a browser <span class=\"hljs-keyword\">on</span> <span class=\"hljs-keyword\">the</span> unix server <span class=\"hljs-keyword\">is</span> inefficient .\n\n<span class=\"hljs-comment\">------- END EMAIL ---</span>\n\nPREDICT: ham\nACTUAL:  ham\n</code></pre>\n<p>We can use the <code>random</code> command to display a random email and have the model classify it.</p>\n<p>So thats it! If youve followed along youve successfully built a spam filter with Python and NTLK. The full source code is available on <a href=\"https://github.com/triedal/spam-filter\">GitHub</a>.</p>\n"}

