	module.exports = {"title":"Roll Your Own: Bootloader Edition","date":"2017-01-10T23:51:47+00:00","layout":"post","path":"/ryo-bootloader/","category":"os dev","description":"How to build a simple bootloader with assembly.","body":"<p>Theres something innately cool about low-level OS development. In order to learn more about this I have built an <em>extremely</em> simple bootloader using assembly language.</p>\n<h3>What Is a Bootloader?</h3>\n<p>When you turn on your computer, the <strong>BIOS</strong> (Basic Input Output System) runs a <strong>POST</strong> (Power On Self Test) test to ensure your machine has ample power, memory, and devices installed. If this test passes, the BIOS then loads a bootloader which is responsible for loading the kernel.</p>\n<p>When the bootloader program is ran it is executed under 16-bit Real Mode. Real Mode means that you have unlimited direct access to all addressable memory. Also, virtual memory and memory protection do not exist at this point.</p>\n<p>In this example we will not be loading a kernel, but instead display a simple Hello, World! on an x86 emulator. However, the concepts learned here are the basis for building a real bootloader.</p>\n<h3>The Nitty-Gritty</h3>\n<p>Here is the full program. I will walk you through it step-by-step below.</p>\n<pre><code><span class=\"hljs-comment\">;-------------</span>\n<span class=\"hljs-comment\">; tinyboot.asm</span>\n<span class=\"hljs-comment\">;-------------</span>\n\n        <span class=\"hljs-meta\">bits</span>    <span class=\"hljs-number\">16</span>                      <span class=\"hljs-comment\">; Declare we are in 16 bit real mode</span>\n\n        org     <span class=\"hljs-number\">0x7c00</span>                  <span class=\"hljs-comment\">; BIOS loads bootloader at this address</span>\n<span class=\"hljs-symbol\">\nstart:</span>\n        <span class=\"hljs-keyword\">jmp</span>         main\n\n\n        \nmsg     <span class=\"hljs-built_in\">db</span>          <span class=\"hljs-string\">\"Hello, World!\"</span>, <span class=\"hljs-number\">0</span>\n<span class=\"hljs-symbol\">\n\nprint:</span>\n        <span class=\"hljs-keyword\">lodsb</span>                            <span class=\"hljs-comment\">; Transfers byte @ DS:SI into AL &amp;&amp; SI++</span>\n        <span class=\"hljs-keyword\">or</span>          <span class=\"hljs-built_in\">al</span>, <span class=\"hljs-built_in\">al</span>\n        <span class=\"hljs-keyword\">jz</span>          return\n        <span class=\"hljs-keyword\">mov</span>         <span class=\"hljs-number\">ah</span>, <span class=\"hljs-number\">0x0e</span>             <span class=\"hljs-comment\">; Load \"Teletype Output\"</span>\n        <span class=\"hljs-keyword\">int</span>         <span class=\"hljs-number\">0x10</span>                 <span class=\"hljs-comment\">; Print character to screen</span>\n        <span class=\"hljs-keyword\">jmp</span>         print\n<span class=\"hljs-symbol\">        \nreturn:</span>\n        <span class=\"hljs-keyword\">ret</span>\n<span class=\"hljs-symbol\">        \nmain:</span>\n        <span class=\"hljs-keyword\">cli</span>                              <span class=\"hljs-comment\">; Disable hardware interrupts</span>\n        <span class=\"hljs-keyword\">mov</span>         <span class=\"hljs-built_in\">si</span>, msg\n        <span class=\"hljs-keyword\">call</span>        print\n        <span class=\"hljs-keyword\">hlt</span>\n\n<span class=\"hljs-built_in\">times</span> <span class=\"hljs-number\">510</span> - ($-$$) <span class=\"hljs-built_in\">db</span> <span class=\"hljs-number\">0</span>                  <span class=\"hljs-comment\">; Fill remaining bytes to 0</span>\n<span class=\"hljs-built_in\">dw</span> <span class=\"hljs-number\">0xAA55</span>                                <span class=\"hljs-comment\">; Boot Signature</span>\n</code></pre>\n<p>Thankfully theres not too much code here. Lets break it down.</p>\n<pre><code>bits    <span class=\"hljs-number\">16</span>\norg     <span class=\"hljs-number\">0x7c00</span> \n</code></pre>\n<p>The first line declares we are using 16 bit mode. All x86 compatible computers boot into 16 bit mode. The second line tells the compiler to ensure all address are relative to <code>0x7c00</code>. The BIOS automatically loads us into <code>0x7c00</code> so we need to account for that.</p>\n<pre><code><span class=\"hljs-symbol\">start:</span>\n        <span class=\"hljs-keyword\">jmp</span>         main\n</code></pre>\n<p>Assembly programs are read line-by-line. We make the first executable command to jump over the rest of our helper operations and into the main part of our program.</p>\n<pre><code><span class=\"hljs-symbol\">main:</span>\n        <span class=\"hljs-keyword\">cli</span>                              <span class=\"hljs-comment\">; Disable hardware interrupts</span>\n        <span class=\"hljs-keyword\">mov</span>         <span class=\"hljs-built_in\">si</span>, msg\n        <span class=\"hljs-keyword\">call</span>        print\n        <span class=\"hljs-keyword\">hlt</span>\n</code></pre>\n<p>In main, we first disable all hardware interrupts. This is necessary for bootloaders that run on x86 machines. Next, we move the address of the first character in <code>msg</code> into the <code>si</code> register. This sets us up to be able to run our print function to print <code>msg</code>. After the address has been loaded into <code>si</code> we call the print function. Lets see what <code>print</code> looks like.</p>\n<pre><code><span class=\"hljs-symbol\">print:</span>\n        <span class=\"hljs-keyword\">lodsb</span>                            <span class=\"hljs-comment\">; Transfers byte @ DS:SI into AL &amp;&amp; SI++</span>\n        <span class=\"hljs-keyword\">or</span>          <span class=\"hljs-built_in\">al</span>, <span class=\"hljs-built_in\">al</span>\n        <span class=\"hljs-keyword\">jz</span>          return\n        <span class=\"hljs-keyword\">mov</span>         <span class=\"hljs-number\">ah</span>, <span class=\"hljs-number\">0x0e</span>             <span class=\"hljs-comment\">; Load \"Teletype Output\"</span>\n        <span class=\"hljs-keyword\">int</span>         <span class=\"hljs-number\">0x10</span>                 <span class=\"hljs-comment\">; Print character to screen</span>\n        <span class=\"hljs-keyword\">jmp</span>         print\n</code></pre>\n<p><code>lodsb</code> loads what is pointed to by <code>si</code> into the <code>al</code> register and increments <code>si</code> by one. We then check if weve reached the end of the null terminated string with <code>or al, al</code>. If we have we return out of the function, if not we keep going. Now we set up to print the first character to the screen. We are going to use the <code>0x10</code> interrupt to print but first we must load <code>0x0e</code> into <code>ah</code>. <code>0x0e</code> is the number of the <em>Teletype Output</em> operation in the <code>0x10</code> interrupt vector. Finally, we call the interrupt and then loop to the top of <code>print</code> again and repeat until weve reached the end of the string.</p>\n<pre><code><span class=\"hljs-symbol\">main:</span>\n        <span class=\"hljs-keyword\">cli</span>                              <span class=\"hljs-comment\">; Disable hardware interrupts</span>\n        <span class=\"hljs-keyword\">mov</span>         <span class=\"hljs-built_in\">si</span>, msg\n        <span class=\"hljs-keyword\">call</span>        print\n--&gt;     <span class=\"hljs-keyword\">hlt</span>\n</code></pre>\n<p>After returning out the print operation we halt the program because we are finished.</p>\n<pre><code><span class=\"hljs-built_in\">times</span> <span class=\"hljs-number\">510</span> - ($-$$) <span class=\"hljs-built_in\">db</span> <span class=\"hljs-number\">0</span>\n<span class=\"hljs-built_in\">dw</span> <span class=\"hljs-number\">0xAA55</span>  \n</code></pre>\n<p>In order for a bootloader to be valid it needs to be <strong>exactly</strong> 512 bytes. The first line ensures that we fill whatever space we didnt use up to 512 with 0s. <code>dw 0xAA55</code> is the <em>Boot Signature</em>. The BIOS looks for this value when determining for a bootable disk. This declares the disk the bootloader is stored on as bootable.</p>\n<h3>Running the Bootloader</h3>\n<p>We will be using an x86 emulator called <a href=\"http://wiki.qemu.org/\">QEMU</a> to test our bootloader.</p>\n<p>First, we must compile the assembly program into a binary. We will be using NASM for this.</p>\n<pre><code class=\"language-sh\">nasm <span class=\"hljs-_\">-f</span> bin tinyboot.asm -o tinyboot.bin\n</code></pre>\n<p>Once we have the binary we will be using <code>dd</code> to convert the binary into a floppy image we can boot from.</p>\n<pre><code class=\"language-sh\">dd conv=notrunc <span class=\"hljs-keyword\">if</span>=tinyboot.bin of=floppy.flp\n</code></pre>\n<p>Almost there! We just need to launch QEMU with our new floppy image to see the magic happen.</p>\n<pre><code class=\"language-sh\">qemu-system-i386 -fda floppy.flp\n</code></pre>\n<figure>\n    <br/>\n\t<img style=\"\" src=\"./qemu.png\" alt=\"Bootloader\">\n\t<figcaption>Sample bootloader running on QEMU</figcaption>\n</figure>\n<p>So there you have itour beautiful bootloader in all its glory!</p>\n<h3>Final Thoughts</h3>\n<p>This was a fun exercise but writing a custom bootloader is not very practical. If you were to develop an OS it is best to use a pre-existing bootloader like <a href=\"https://www.gnu.org/software/grub/\">GRUB</a>. Also, if you would like to learn more about bootloaders and OS development in general <a href=\"http://www.brokenthorn.com/\">BrokenThorn</a> and <a href=\"http://wiki.osdev.org/Main_Page\">OS Dev</a> are excellent resources.</p>\n<p>Source code available on <a href=\"https://github.com/triedal/misc/blob/master/bootloader-example/tinyboot.asm\">GitHub</a>.</p>\n"}

