	module.exports = {"title":"Compiling The Vanilla Linux Kernel","date":"2017-03-14T23:51:47+00:00","layout":"post","path":"/compile-linux-kernel/","category":"os dev","description":"Guide to building the Linux kernel.","body":"<p>Today Im going to walk you through how to build the vanilla Linux kernel  step-by-step. Typically you will never need to compile your own kernel but its interesting to know how its done regardless. As a quick note, the Linux kernel we are going to be building today is different than the kernel you get from your distribution (Ubuntu, Debian, etc). This is the raw kernel that comes from Linux Torvalds himself. Linux kernels shipped from distributions are modified versions of the mainline kernel that include patches and customizations from the distributor.</p>\n<p>I will be building the latest stable kernel (4.10.2 at the time of this writing) on Ubuntu 14.04 so these steps may not be exactly correct for you. However, the general process is the same.</p>\n<h3>Prerequisites</h3>\n<p>In order to compile the kernel we will need some tools. Lets get those.</p>\n<pre><code class=\"language-bash\">sudo apt-get install build-essential ncurses-dev libssl-dev wget\n</code></pre>\n<p>Well also need to get the kernel source and create a working directory.</p>\n<pre><code class=\"language-bash\">mkdir linux &amp;&amp; <span class=\"hljs-built_in\">cd</span> linux\nwget https://cdn.kernel.org/pub/linux/kernel/v4.x/linux-4.10.2.tar.xz\n\n</code></pre>\n<h3>Lets Get Started</h3>\n<p>First, well need to extract the kernel from the tarball. Once it is extracted we can delete the tarball as it is no longer needed.</p>\n<pre><code class=\"language-bash\">tar xf linux-4.10.2.tar.xz\nrm <span class=\"hljs-_\">-f</span> linux-4.10.2.tar.xz\n</code></pre>\n<p>Now <code>cd</code> into the extracted kernel directory.</p>\n<h3>Kernel Configuration</h3>\n<p>The Linux kernel requires a <code>.config</code> file for building. We will need to create this. You can manually create the config by going through each of the thousands of options one-by-one but that is not recommended. Instead we are going to use the config for our host OS as a baseline.</p>\n<pre><code class=\"language-bash\">cp /boot/config-$(uname -r) .config\nyes <span class=\"hljs-string\">\"\"</span> | make oldconfig\n</code></pre>\n<p>The above copies the host OS config into the config used for the kernel we are building. We then answer yes to any new configuration options in the vanilla kernel.</p>\n<h3>Compiling</h3>\n<p>We are now ready to compile the kernel. This is going to take some time so go grab some coffee. When compiling the kernel we have the option to specify how many cores we should use when compiling. Check how many cores you have available with the <code>nproc</code> command. I have 8 so I am going to use 6 of those for the compiling task.</p>\n<pre><code class=\"language-bash\">make -j6\n</code></pre>\n<p>Hopefully your kernel compiled successfully. If it did you can move onto the next step. We now have to compile and install the kernel modules.</p>\n<pre><code class=\"language-bash\">sudo make modules_install install\n</code></pre>\n<p>Congratulations! If youve made it this far youve successfully compiled a vanilla Linux kernel that is ready for use. From here you can update grub to boot into your new kernel. However, I will be booting the kernel using the qemu emulator. I plan on writing a follow-up tutorial for this in the near future.</p>\n"}

