	module.exports = {"title":"Subverting the Linux Kernel for Fun and Profit","date":"2017-04-22T23:51:47+00:00","layout":"post","path":"/rootkit-poc/","category":"security","description":"Demonstration of a simple rootkit developed for the 2.6.x Linux kernel.","body":"<p>Ive built a simple rootkit for the 2.6.x Linux kernel that has the ability to hide itself as well as hide processes. Its fairly simple feature-wise but gave me a more insight into how rootkits work and allowed me to get my hands dirty in Linux kernel module development.</p>\n<p>Im not going to go into the specifics of how the code works, rather Im just going to give a quick demo of the rootkit in action.</p>\n<h3>What Is a Rootkit?</h3>\n<p>First lets explain what a rootkit is and how it relates to security. According to <a href=\"https://www.veracode.com/security/rootkit\">Veracode</a>:</p>\n<blockquote>\n<p>A rootkit is a clandestine computer program designed to provide continued privileged access to a computer while actively hiding its presence. The term rootkit is a connection of the two words root and kit. Originally, a rootkit was a collection of tools that enabled administrator-level access to a computer or network. Root refers to the Admin account on Unix and Linux systems, and kit refers to the software components that implement the tool. Today rootkits are generally associated with malware  such as Trojans, worms, viruses  that conceal their existence and actions from users and other system processes.</p>\n</blockquote>\n<p>So originally rootkits were used to provide remote administrator-level access to systems for legitimate uses. Schools and businesses were typical users of these rootkits. However, over time rootkits found their home in malware. A rootkit installed on a victims machine can conceal itself and allow remote access to the machine via a backdoor.</p>\n<h3>Avoiding Detection</h3>\n<p>To avoid detection, a rootkit must able to hide itself from the typical places you would be able to list installed kernel modules. This includes <code>/proc/modules</code> and <code>/sys/module</code>. As you can see in the figure below, the rootkit is not able to be found through <code>lsmod</code> after giving it the <code>modhide</code> command. All commands are issued to the rootkit through a character device.</p>\n<figure>\n    <br/>\n\t<img style=\"\" src=\"./modhide.gif\" alt=\"modhide cmd\">\n\t<figcaption>modehide command</figcaption>\n</figure>\n<p>To be able to uninstall the rootkit we must first reveal it by sending it the <code>modshow</code> command. Now we can safely use <code>rmmod</code> to remove it.</p>\n<h3>Hiding Processes</h3>\n<p>The rootkit also has the ability to hide currently running processes. This can be achieved by issuing the <code>phide</code> command with a PID as an argument. Lets see this in action.</p>\n<figure>\n    <br/>\n\t<img style=\"\" src=\"./phide.gif\" alt=\"phide cmd\">\n\t<figcaption>phide command</figcaption>\n</figure>\n<p>Just like with <code>modhide</code>, we have a corresponding <code>pshow</code> command to reveal the process.</p>\n<h3>Final Thoughts</h3>\n<p>There are many features that a typical rootkit would include that are not included in this implementation. This is by no means complete and is just a simple example implementation.</p>\n<p>Source code available on <a href=\"https://github.com/triedal/rootkit\">GitHub</a>.</p>\n"}

