	module.exports = {"title":"Books","layout":"page","path":"/books","body":"<p>I enjoy reading a lot and will try to use this page to write down what I believe are the most important take-aways from each book I read. Most of the books I read are technical books or biographies.</p>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./banana-man.jpg\">\n  <h3>The Fish That Ate the Whale: The Life and Times of America's Banana King</h3>\n  <em>By Rich Cohen</em>\n  <p>Know you business from end to end. Sam was in the field with his workers instead of managing it from a distant office. He knew every aspect of his business and as a result he continually made better decisions than his competitors. Also, one man's trash is another man's treasure. Sam started his business by purchasing ripe bananas from larger companies that were deemed worthless. The ripes would not make it to their desitination before rotting. Sam bought them and sold them directly out of the rail car.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./make-shit-happen.jpg\">\n  <h3>How to Make Sh*t Happen</h3>\n  <em>By Sean Whalen</em>\n  <p>Every thing you do relates to one of the following categories: body, relationships, mind, or business. Take action each day to work on each one of these categories. Small incremental improvements will snowball into massive results.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./tao.jpg\">\n  <h3>The Tao of Microservices</h3>\n  <em>By Richard Rodger</em>\n  <p>To build a scalable microservice architecture you must ensure you have transport independence. Microservices should NOT know about each other. Microservices must be able to send messages into the ether and a seperate transport layer will route them to the correct listening microservice. Also, when designing a microservice architecture think about what the messages are before figuring out what microservices you will have. Once you have listed all the messages the microservices to support those messages will be obvious.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./onward.jpg\">\n  <h3>Onward</h3>\n  <em>By Howard Schultz</em>\n  <p>It was so long ago that I read this one that I don't remember any key take-aways but I do remember highly enjoying this book. It was interesting to see the rise of Starbucks from a single coffee shop to a coffee empire.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./win-friends.jpg\">\n  <h3>How to Win Friends &amp; Influence People</h3>\n  <em>By Dale Carnegie</em>\n  <p>This book is a classic. It was published in 1932 and its lessons are still applicable today. This book shows you how to be a likable, persuasive, and influental leader. Read this once a year.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./brain.jpg\">\n  <h3>What Makes Your Brain Happy and Why You Should Do the Opposite</h3>\n  <em>By David DiSalvo</em>\n  <p>Our brain is wired to take the easiest route. However, this may not be the best outcome for us. DiSalvo urges readers to think critically about why we may be doing or thinking certain things.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./losing-virginity.jpg\">\n  <h3>Losing My Virginity: How I Survived, Had Fun, and Made a Fortune Doing Business My Way</h3>\n  <em>By Richard Branson</em>\n  <p>Richard Branson is a hustler. The most important theme I could derive from his business ventures is to create massive value for people and your business will prosper. I also found his adventures/stunts incredibly interesting.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./outliers.jpg\">\n  <h3>Outliers: The Story of Success</h3>\n  <em>By Malcolm Gladwell</em>\n  <p>This was a great read. Gladwell proposes that we pay too much attention to what successful people are like, and too little attention to where they are from. He argues that success is the result of chance opportunity and the preparation to be able to take advantage of that opportunity.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./third-circle.jpg\">\n  <h3>Third Circle Theory: Purpose Through Observation</h3>\n  <em>By Pejman Ghadimi</em>\n  <p>One of the first \"success\" books I ever read. I don't remember much. I'll have to re-read this and update later.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./100-startup.jpg\">\n  <h3>The $100 Startup: Reinvent the Way You Make a Living, Do What You Love, and Create a New Future</h3>\n  <em>By Chris Guillebeau</em>\n  <p>A bunch of short accounts of different people who built businesses with very little money.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./lucky.jpg\">\n  <h3>I'm Feeling Lucky: The Confessions of Google Employee Number 59</h3>\n  <em>By Douglas Edwards</em>\n  <p>LP &amp; SB made decisons based on data. They also placed a large emphasis on always learning. Employees were on a \"need to know\" basis. Why slow down to explain when the value of what LP &amp; SB were doing was so self-evident that people would eventually see it for themselves? If you are not adding value, get out of the way.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./zero-to-one.jpg\">\n  <h3>Zero to One: Notes on Startups, or How to Build the Future</h3>\n  <em>By Peter Thiel</em>\n  <p>True progress can only be made with new technology. New technology is something that has not existed before, not a spin off of something else (i.e. - we are the Uber of...). Corporations are too large to make any real progress. A startup is the largest group of people you can convice of a plan to build a different future. Startups should make every person in the company responsible for doing just one thing. This reduces fights over responsibilities. Sales is just as important as building the product.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./alchemist.jpg\">\n  <h3>The Alchemist</h3>\n  <em>By Paulo Coelho</em>\n  <p>I don't like fiction but this was a great book. Will probably read again.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./reliable-datastructures.jpg\">\n  <h3>Reliable Data Structures in C</h3>\n  <em>By Thomas Plum</em>\n  <p>The title is pretty self explainitory. Read this is you're a CS student.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./k-r.jpg\">\n  <h3>C Programming Language, 2nd Edition</h3>\n  <em>By Brian W. Kernighan &amp; Dennis M. Ritchie</em>\n  <p>I was lucky enough to get my hands on an original copy of this. This is <em>the</em> C bible. Read this is you're a CS student.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./intelligent-investor.jpg\">\n  <h3>The Intelligent Investor: The Definitive Book on Value Investing</h3>\n  <em>By Benjamin Graham</em>\n  <p>Buy index funds.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./data-structures.jpg\">\n  <h3>Data Structures in C</h3>\n  <em>By Noel Kalicharan</em>\n  <p>Semi-modern read on data structures. Read this if you're a CS student.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./html.jpg\">\n  <h3>HTML and CSS: Design and Build Websites</h3>\n  <em>By Jon Duckett</em>\n  <p>This one holds a special place in my heart. This was the first \"programming\" book I ever read (I know HTML isn't programming). Super easy read if you're an aspiring web developer. HTML &amp; CSS are the foundations.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./jobs.jpg\">\n  <h3>Steve Jobs</h3>\n  <em>By Walter Isaacson</em>\n  <p>I don't remember many of the details about this book but it was an enjoyable read.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./musk.jpg\">\n  <h3>Elon Musk: Tesla, SpaceX, and the Quest for a Fantastic Future</h3>\n  <em>By Ashlee Vance</em>\n  <p>Elon Musk is a super human. He's a wicked smart engineer with business intelligence. I don't know how he accomplishes the things he does. Elon is an active learner like LP &amp; SB. He accepts nothing less than excellence.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./alg-to-live-by.jpg\">\n  <h3>Algorithms to Live By: The Computer Science of Human Decisions</h3>\n  <em>By Brian Christian</em>\n  <p>It was interesting to see how to apply the things I learned in CS classes to real life.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./wolf.jpg\">\n  <h3>The Wolf of Wall Street</h3>\n  <em>By Jordan Belfort</em>\n  <p>Highly entertaining read. Way better than the movie (although the movie is pretty great).</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./astronaut-guide.jpg\">\n  <h3>An Astronaut's Guide to Life on Earth: What Going to Space Taught Me About Ingenuity, Determination, and Being Prepared for Anything </h3>\n  <em>By Chris Hadfield</em>\n  <p>Chris Hadfield was born an Canadian and it was very likely he would never go to space. However, that did not stop him from pursuing his dream. Hadfield stresses to always be prepared for the opportunity to realize your dreams but don't hang your self-worth on reaching that goal. Enjoy the ride, not the destination.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./lean-startup.jpg\">\n  <h3>The Lean Startup: How Today's Entrepreneurs Use Continuous Innovation to Create Radically Successful Businesses</h3>\n  <em>By Eric Ries</em>\n  <p><em>The Lean Startup</em> devulges a plan that companies can use to engineer success. This plan focuses on experimentation, feedback, interative design, and small units of work. The backbone of this plan is the <em>build-measure-learn</em> feedback loop. Startups should use this to turn ideas into products, measure customers' responses, and decide whether to pivot or persevere. Highly recommend reading this.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./subconscious-mind.jpg\">\n  <h3>The Power of Your Subconscious Mind</h3>\n  <em>By Joseph Murphy</em>\n  <p>Interesting book about Law of Attraction. Highly repetitve but has many great points. The gist is what you impress on your subconscious mind will become your realty.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./how-to-american.jpg\">\n  <h3>How to American: An Immigrant's Guide to Disappointing Your Parents</h3>\n  <em>By Jimmy O. Wang</em>\n  <p>Inspiring an hilarious memoir about growing up in America as a Chinese immigrant and making it in Hollywood. Jimmy has achieved the \"American Dream\" by turning down a mundane job in finance and pursuing his dreams of being a stand-up comedian. Through his pursuit of a comedy career Jimmy was able to meet many of his childhood heroes and become an actor in top TV shows and movies such as <em>Silicon Valley</em> and <em>Patriot's Day</em>.</p>\n</div>\n<div class=\"book\">\n  <img class=\"thumb\" src=\"./rework.jpg\">\n  <h3>Rework</h3>\n  <em>By Jason Fried &amp; David Heinemeier Hansson</em>\n  <p><em>Rework</em> challenges conventional business wisdom. You don't need a business plan, you don't need funding, and you don't need a large team. One of my favorite pieces of advice in this book is \"Good enough is fine\". When facing an obstacle, look for a solution that delivers maximum efficiency with minimum effort. As an engineer, I think we have the tendency to create over optimized solutions to problems because it's sexy. However, these solutions do not necessarily contribute more added value to the bottom line and are just wasted time and effort. <em>Rework</em> also refutes the idea of workaholism. Workaholics don't accomplish more than nonworkaholics. Come up with an effienct solution rather than brute forcing more hours into it. This book goes hand-in-hand with <em>The Lean Startup</em>.</p>\n</div>\n"}

